import React, { useEffect, useState } from 'react';
import s from './profileStatus.module.scss'

const ProfileStatusWithHooks = (props) =>  {
    const {updateStatus} = props;

    let [editMode, changeEditMode] = useState(false);
    let [status, setStatus] = useState(props.status);

    useEffect(()=>{
        setStatus(props.status);
    }, [props.status])

    const toggleEditMode = (boolean) => {
        changeEditMode(boolean);
    }

    const getText = (e) => {
        setStatus(e.currentTarget.value)
    }

    const newStatus = () => {
        toggleEditMode(false)
        updateStatus(status);
    }

    return (
        <div className={s.status}>
            { editMode ? 
                <div>
                    <div className={s.editor}>
                        <input autoFocus onBlur={newStatus} value={status} onChange={getText}></input>
                        <button onClick={() => newStatus()}>Сохранить</button>
                    </div>
                    <div onClick={()=> toggleEditMode(true)}>
                        <span>
                            {status ? status : 'Установить статус'}
                        </span>
                    </div> 
                </div>
                : 
                <div onClick={()=> toggleEditMode(true)} className={s.statusSpan} >
                    <span>
                        {status ? status : 'Установить статус'}
                    </span>
                </div> 
            }
        </div>
    )
}

export default ProfileStatusWithHooks;