import React from 'react';
import { Field } from 'redux-form';
import { Input } from '../../../../../common/Textarea/formChilds';
import { requiredField } from '../../../../../utils/validator';

export const ProfileDataItem = ({item, itemDescr, plug, s}) => {
    return (
        <div className={s.profileItem}>
            <div className={s.aboutItem}>{itemDescr}</div><div className={s.aboutDescr}>{{item} ? item : {plug} }</div>
        </div>
    )
}
export const ProfileDataItemCheckbox = ({item, itemDescr, search, dontSearch, s}) => {
    return (
        <div className={s.profileItem}>
            <div className={s.aboutItem}>{itemDescr}</div><div className={s.aboutDescr}>{item ? search : dontSearch }</div>
        </div>
    )
}

export const ProfileDataItemInput = ({itemDescr, name, placeholder, type, s}) => {
    return (
        <div className={s.profileItem}><div className={s.aboutItem}>{itemDescr}</div><div className={s.aboutDescr}><Field type={type ? 'checkbox' : 'input'} component={Input} placeholder={placeholder} name={name} validate={[requiredField]} /></div></div>
    )
}