import {Component} from 'react';
import Posts from '../MyPosts';
import ProfileInfo from '../ProfileInfo/profileInfo';
import Loading from '../../../common/loading/loading';
import { Redirect } from 'react-router';



class Profile extends Component {
    refreshProfile() {
        const props = this.props;
        const {setProfile, setStatus, match} = props;
        
        let userId = match.params.userId;
        if (!userId) {
            userId = props.userId;
        }
        setProfile(userId);
        setStatus(userId);
    }
    componentDidMount() {
        this.refreshProfile();
    }
    componentDidUpdate(prevProps) {
        if (this.props.match.params.userId !== prevProps.match.params.userId) {
            this.refreshProfile();
        }
    }
    render () {
        const props = this.props;
        const { addPost, changePostText, newPostText, profile, posts, match, sendPhoto, updateProfileData, userId, deletePost, likePost, disLikePost, setPicturePreview, picturePreview } = props; /* profile, posts */
        const {status, editMode, toggleEditMode, newStatusText, updateStatus} = props; /* status */
        let isOwner = !match.params.userId;

        if (!profile) {
            return (<Loading/>)
        }

        if (+match.params.userId === userId) {
            return <Redirect to='/profile/' />
        }

        return (
            <div>
                <ProfileInfo updateProfileData={updateProfileData} sendPhoto={sendPhoto} isOwner={isOwner} addPost={addPost} changePostText={changePostText} newPostText={newPostText} profile={profile} status={status} statusEditMode={editMode} toggleEditModeStatus={toggleEditMode} newStatusText={newStatusText} updateStatus={updateStatus} setPicturePreview={setPicturePreview} picturePreview={picturePreview} />
                <Posts posts={posts} profile={profile} deletePost={deletePost} likePost={likePost} disLikePost={disLikePost} isOwner={isOwner} />
            </div>
        )
    }
}
export default Profile;