import s from './uploadPhoto.module.scss';
export const UploadPhotoInput = ({setPicturePreview}) => {
    
    const onGetNewFile = (e) => {
        if(e.target.files.length) {
            let file = e.target.files[0];
            let formData = new FileReader();
            formData.onload = (function(e) {
                return function(e) {
                   let r = e.target;
                   console.log(r.result)
                   setPicturePreview(r.result)
                   return r
                }
            })(file)
            formData.readAsDataURL(file)
        }
    }

    return (
        <div className={s.uploadPhotoBtn}>
            <input type={'file'} onChange={onGetNewFile} />
        </div>
    )
}