import React from 'react';
import Post from './Post';
import notEnoughPic from './icons/no_posts.png';
import p from './posts.module.scss';

const Posts = React.memo(({ posts, profile, deletePost, likePost, disLikePost, isOwner }) => {

    let { fullName, photos, userId } = profile;

    let newPosts = posts.map(p => <Post key={p.id} id={p.id} message={p.message} name={fullName} avatar={photos.small} likes={p.likes} userId={userId} deletePost={deletePost} likePost={likePost} liked={p.liked} disLikePost={disLikePost} picture={p.picture} />)
    
    let newPostsLength = newPosts.length;

    if (!isOwner) {
        return (
            <div className={p.postsWrapper}>
                <div className={p.postsHeader}>
                    <PostsCount p={p} posts={[]} />
                </div>
                <div className={p.posts}>
                    <NotEnoughPosts />
                </div>
            </div>
        )
    }

    return (
        <div className={p.postsWrapper}>
            <div className={p.postsHeader}>
                <PostsCount p={p} posts={posts} />
            </div>
            <div className={p.posts}>
                {newPostsLength ? newPosts : <NotEnoughPosts />}
            </div>
        </div>
    )
})

let PostsCount = ({p, posts}) => {

    let postsLength = posts.length;

    let noteBeforeFive = postsLength === 1 ? 'запись' : null;
    let noteAfterFive = postsLength > 1 && postsLength < 5 ? 'записи' : 'записей' ;

    return (
        <div className={p.postsHeader__item}>{postsLength} {noteBeforeFive ? noteBeforeFive : noteAfterFive}</div> 
    )
}

let NotEnoughPosts = () => {
    return (
        <div className={p.notEnough}>
            <div className={p.notEnoughWrap}>
                <div className={p.notEnoughPic}>
                    <img src={notEnoughPic} alt="notEnoughPostPic" />
                </div>
                <div className={p.notEnoughText}>На стене пока нет не единой записи</div>
            </div>
        </div>
    )
}

export default Posts;