import React from 'react';
import { reduxForm } from 'redux-form';
import { ContactsEdit } from '../Contacts/contacts';
import { ProfileDataItemInput } from '../ProfileData/profileDataItem/profileDataItem';


let ProfileDataForm = ({s, handleSubmit, profile, turnOffEditMode, isOwner}) => {
    return (
        <form onSubmit={handleSubmit} className={s.profileFooter}>
            <div className={s.profileDescr}><div className={s.editInfoTitle}>Основная информация</div>{isOwner ? <div className={s.backButton} onClick={turnOffEditMode}>Назад</div> : null}</div>
            <ProfileDataItemInput itemDescr='Полное имя:' name='fullName' placeholder="Полное имя" s={s} />
            <ProfileDataItemInput itemDescr='Обо мне:' name='aboutMe' placeholder="Обо мне" s={s} /> 
            <div className={s.profileDescr}><div className={s.editInfoTitle}>Рабочая информация</div></div>
            <ProfileDataItemInput itemDescr='Ищу Работу:' name='lookingForAJob' placeholder="Ищу работу" type='checkbox' s={s} />
            <ProfileDataItemInput itemDescr='Мои навыки:' name='lookingForAJobDescription' placeholder="Опишите работу" s={s}/> 
            <div className={s.profileDescr}><div className={s.editInfoTitle}>Контактная информация</div></div>
            {Object.keys(profile.contacts).map( key => { return <ContactsEdit key={key} contactTitle={key} placeholder={profile.contacts[key]} name={'contacts.' + key} s={s} /> })}
            <button type="submit" >Сохранить</button>
        </form>
    )
}
ProfileDataForm = reduxForm({
    form: 'profileData'
})(ProfileDataForm)
export default ProfileDataForm;