import { reduxForm, Field } from 'redux-form';
import {maxLengthCreator, requiredField} from '../../../../utils/validator';
import { TextArea } from '../../../../common/Textarea/formChilds';
import useComponentVisible from '../../../../utils/opener/opener';
import { UploadPhotoInput } from '../../../../common/uploadPhoto/uploadPhoto';


const maxLength = maxLengthCreator(100);
let PostsForm = ({ handleSubmit, avatar, isOwner, p, setPicturePreview, picturePreview}) => {
    const {ref, isComponentVisible, setIsComponentVisible} = useComponentVisible(false);

    const changeWriteMode = (boolean) => {
        setIsComponentVisible(boolean)
    }

    return (
        <form onSubmit={handleSubmit} className={p.profileFooter__postwrite}>
            <div className={p.profile__addPostAvatar}>
                <img src={avatar != null ? avatar : 'https://html5css.ru/w3images/avatar2.png'} alt="myMiniAvatar" />
            </div>
            { 
                isOwner ? 
                <div className={p.profile__addPostTextarea} ref={ref}>
                    <Field
                        name="postInput"
                        component={TextArea}
                        type="text"
                        placeholder="Чем хотите поделиться?"
                        validate={[requiredField, maxLength]}
                        
                        onClick={() => changeWriteMode(true)}
                    />
                    {picturePreview ? <img className={p.previewPicture} src={picturePreview} alt="uploadedPhoto" /> : ''}
                    {isComponentVisible || picturePreview ? <ProfilePostFormButtons p={p} setPicturePreview={setPicturePreview} /> : null}
                </div> 
                :
                <div className={p.profile__addPostTextarea} ref={ref}>
                    <Field
                        name="postInput"
                        component={TextArea}
                        type="text"
                        placeholder="Напишите что нибудь..."
                        validate={[requiredField, maxLength]}
                        
                        onClick={() => changeWriteMode(true)}
                    />
                    {isComponentVisible ? <button type="sumbit" disabled >Опубликовать</button> : null}
                </div>
            }
        </form>
        
    )
}
PostsForm = reduxForm({
    form: 'post-form'
})(PostsForm)

let ProfilePostFormButtons = ({p, setPicturePreview}) => {
    return (
        <div className={p.buttons}>
            <UploadPhotoInput setPicturePreview={setPicturePreview} />
            <button type="sumbit" >Опубликовать</button>
        </div>
    )
}

export default PostsForm;