import React from 'react';
import { NavLink } from 'react-router-dom';
import postStyle from './post.module.scss'
const Post = ({message, likes, name, avatar, id, userId, deletePost, likePost, liked, disLikePost, picture}) => {
    return (
            <div id={id} className={postStyle.post}>
                <div className={postStyle.head}>
                    <div className={postStyle.avatar}>
                        <img src={avatar != null ? avatar : 'https://html5css.ru/w3images/avatar2.png'} alt="myMiniAvatar" />
                    </div>
                    <div className={postStyle.right}>
                        <NavLink className={postStyle.name} to={'/profile/' + userId} >{name}</NavLink>
                        <div className={postStyle.time}>Сегодня в 13:37</div>
                    </div>
                    <div className={postStyle.delete} onClick={ () => deletePost(id)} >&#10006;</div>
                </div>
                <div className={postStyle.message}>
                    <div className={postStyle.postText}>{message}</div>
                    <img src={picture} alt={picture} />
                </div>
                <div className={postStyle.footer}>
                    {
                        liked ?
                        <div className={postStyle.likes}>
                            <button className={postStyle.like_active} onClick={() => disLikePost(id, likes)} /><div className={postStyle.number_active}>{likes}</div>
                        </div> : 
                        <div className={postStyle.likes}>
                            <button className={postStyle.like} onClick={() => likePost(id, likes)} /><div className={postStyle.number}>{likes}</div>
                        </div>
                    }
                </div>
            </div>
    )
}
export default Post;