import { connect } from 'react-redux';
import { withRouter } from 'react-router';
import { compose } from 'redux';
import { addPost, changePostText, getProfile, toggleEditMode, setProfile, setStatus, newStatusText, updateStatus, sendPhoto, updateProfileData, deletePost, likePost, disLikePost, setPicturePreview } from '../../redux/reducers/profileReducer/profileReducer';
import { isAuthSelector, userIdSelector } from '../../redux/selector/authSelector';
import { editModeSelector, newPostTextSelector, postsSelector, profileSelector, statusSelector } from '../../redux/selector/profileSelector';
import { withAuthRedirect } from '../hoc/withAuthRedirect';
import Profile from './Profile';
let mapStateToProps = (state) => {
    return {
        posts: postsSelector(state),
        newPostText: newPostTextSelector(state),
        profile: profileSelector(state),
        status: statusSelector(state),
        picturePreview: state.profileReducer.picturePreview,
        editMode: editModeSelector(state),
        isAuth:isAuthSelector(state),
        userId: userIdSelector(state)
    }
}
export default compose(
    connect(mapStateToProps, {addPost,changePostText, getProfile, toggleEditMode, newStatusText, setProfile, setStatus, updateStatus, sendPhoto, updateProfileData, deletePost, likePost, disLikePost, setPicturePreview }),
    withRouter,
    withAuthRedirect
)(Profile);