import React from 'react';
const Avatar = ({isOwner, onGetNewFile, profile, s}) => {
    return (
        <div className={s.avatarInner}>
            <div className={s.avatar}>
                <img src={profile.photos.large ? profile.photos.large : 'https://html5css.ru/w3images/avatar2.png'} alt="myAvatar" />
            </div>
            {isOwner && <AvatarBtn onGetNewFile={onGetNewFile} s={s} />}
        </div>
    )
}
export default Avatar;

const AvatarBtn = ({onGetNewFile, s}) => {
    return (
        <button className={s.avatarWrap}><input className={s.avatarInput} type={'file'} onChange={onGetNewFile} /> Выбать файл</button>
    )
}