import React, { useState } from 'react';
import { Contacts } from '../Contacts/contacts';
import { ProfileDataItem, ProfileDataItemCheckbox } from './profileDataItem/profileDataItem';

export const ProfileData = ({profile, isOwner, goToEditMode, s}) => {

    let [moreInfo, toggleMoreInfo] = useState(false);

    const showMoreInfo = (boolean) => {
        toggleMoreInfo(boolean)
    }

    const {fullName, aboutMe, lookingForAJob, lookingForAJobDescription} = profile;
    return (
        <div className={s.profileFooter}>
            <div className={s.upStage}>
                <div className={s.profileDescr}><div className={s.editInfoTitle}>Основная информация</div>{isOwner ? <div className={s.editButton} onClick={goToEditMode}>Редактировать</div> : null}</div>
                <ProfileDataItem itemDescr='Полное имя:' item={fullName} plug='Имя' s={s} />
                <ProfileDataItem itemDescr='Обо мне:' item={aboutMe} plug='Инфы нет ;(' s={s} />
            </div>
            {moreInfo ? <div className={s.moreInfoBtn} onClick={ () => showMoreInfo(false)} >Скрыть подробную информацию</div> : <div className={s.moreInfoBtn} onClick={ () => showMoreInfo(true)} >Показать подробную информацию</div>}
            <div className={moreInfo ? s.downStage_active : s.downStage}>
                <div className={s.profileDescr}><div className={s.editInfoTitle}>Рабочая информация</div></div>
                <ProfileDataItemCheckbox itemDescr='Ищу работу:' item={lookingForAJob} search='Ищу' dontSearch='Не ищу' s={s} />
                <ProfileDataItem itemDescr='Мои навыки:' item={lookingForAJobDescription} plug='Нет описания' s={s} />
                <div className={s.profileDescr}><div className={s.editInfoTitle}>Контактная информация</div></div>
                {Object.keys(profile.contacts).map( key => { return <Contacts key={key} contactTitle={key} contactValue={profile.contacts[key]} s={s} />})}
            </div>
        </div>
    )
}