import React, { useState } from 'react';
import pStyle from './profileInfo.module.scss';
import ProfileStatusWithHooks from './ProfileStatus/profileStatusWithHooks';
import ProfileDataForm from './profileDataForm/profileDataForm';
import { ProfileData } from './ProfileData/profileData';
import Avatar from './profileInfoAvatar/profileInfoAvatar';
import PostsForm from './postsForm/postsForm';


const ProfileInfo = ({status, statusEditMode, toggleEditModeStatus, newStatusText, updateStatus, addPost, profile, isOwner, sendPhoto, updateProfileData, setPicturePreview, picturePreview}) => {

    let [editMode, changeEditMode] = useState(false);

    let addNewPost = (values, file) => {
        addPost(values.postInput, file);
    }

    const onGetNewFile = (e) => {
        if (e.target.files.length) {
            sendPhoto(e.target.files[0])
        }
    }

    const onSubmit = async (formData) => {
        await updateProfileData(formData)
        changeEditMode(false)
    } 
    
    return (
        <div className={pStyle.profileInfo}>
            <Avatar isOwner={isOwner} onGetNewFile={onGetNewFile} s={pStyle} profile={profile} />
            <div className={pStyle.profileInner}>
                <div className={pStyle.profileHeader}>
                    <div className={pStyle.profileHeader__name}>
                        {profile.fullName}
                    </div>
                    <div className={pStyle.profileHeader__status}>
                        <ProfileStatusWithHooks status={status} editMode={statusEditMode} toggleEditMode={toggleEditModeStatus} newStatusText={newStatusText} updateStatus={updateStatus}/>
                    </div>
                    {
                        editMode ? 
                            <ProfileDataForm turnOffEditMode={() => changeEditMode(false)} initialValues={profile} s={pStyle} onSubmit={onSubmit} profile={profile} isOwner={isOwner} /> 
                                : 
                            <ProfileData goToEditMode={() => changeEditMode(true)} isOwner={isOwner} profile={profile} s={pStyle} />
                    }
                </div>
                <PostsForm setPicturePreview={setPicturePreview} picturePreview={picturePreview} onSubmit={addNewPost} avatar={profile.photos.small} isOwner={isOwner} p={pStyle} />
            </div>
        </div>
    )
}

export default ProfileInfo;